import React, { useState } from 'react'
import logo from '../img/logo.jpg';
import { Link,NavLink } from 'react-router-dom';
import userLogo from './user-128.svg';
import './Navbar.css';
import Login from './Login/Login';
import cart from './Payment/cart.png';
import { useDispatch, useSelector } from 'react-redux';
import { unSetUser } from '../Store/userSlice';
import './NavbarHamburger.css';


function Navbar() {

  const dispatch = useDispatch();
  let UserName = useSelector((state)=>state.Auth.user.name);
  console.log(UserName);

  const [mobile, setMobile] = useState(false);
  const [login, setLogin] = useState(false);

  function logout(){
    dispatch(unSetUser());
    localStorage.clear();
  }

  
  return (
    <>
      {login ? <Login visiblity={setLogin}></Login> : ""}

      <div className='navbar'>
        <img className="logo" src={logo} />
     
        <ul className={mobile ? "nav-links-mobile" : 'nav-links'}
          onClick={() => setMobile(false)}>
          <Link to="/" className='home'><li>Home</li></Link>
          <Link to="/about" className='about'><li>About</li></Link>
          <Link to="/product" className='gallery'><li>Product</li></Link>
          
          {
            UserName ?
             <>
            <Link className='service UserNav' id='userNav'><img src={userLogo} alt="UserLogo" /><li>{UserName.split(" ")[0]}</li></Link>
            <Link className='service UserNav' id='userNav' onClick={logout}><li>Logout</li></Link>
            </>
            :
            <>
            <Link to="/signin" className='service'><li>Signup</li></Link>
            <Link  className='service'  onClick={()=>{setLogin(!login)}}><li>Login</li></Link>
            </>
          }
          
          
          <Link to={'/cart'} className='cart'><li>
            Cart
            <img src={cart} alt="cart" />
          </li></Link>
        </ul>

        {/* Hamburger */}
    <input id="menu-toggle" type="checkbox" />
    <label class='menu-button-container' for="menu-toggle">
    <div className="menu-c"><div class='menu-button'></div></div>
  </label>
    <ul class="menu">
       <li><Link to={'/'}>Home</Link></li>
       <li><Link to={'/about'}>About</Link></li>
       <li><Link to={'/cart'}>Cart</Link></li>
       <li><Link to={'/product'}>products</Link></li>
       {
            UserName ?
             <>
            <li><Link ><img src={userLogo} alt="UserLogo" />venom</Link></li>
            <li><Link  onClick={logout}>Logout</Link></li>
            </>
            :
            <>
            <li><Link to="/signin" >Signup</Link></li>
            <li><Link onClick={()=>{setLogin(!login)}}>Login</Link></li>
            </>
          }
    </ul>

      </div>
    </>

  )
}


export default Navbar