import { useEffect, useState } from "react";
import {useDispatch, useSelector} from 'react-redux';
import { setCounter , setProductArray } from "../../Store/CartSlice";
import img from './../34778a.webp';

function CartProduct({data}){
const dispatch  = useDispatch();
const cart = useSelector(s=>s.Cart);

function changeCounterHandler(e){
  dispatch(setCounter({id:data.productID,value:e.target.value}));
}

function removeHandler(){
  let itemToLeft = cart.products.filter((e)=>{
      return  e.productID != data.productID
  });
  
  console.log(itemToLeft);

  dispatch(setProductArray(itemToLeft));
}

return <div class="product">
  <div class="product-image">
    <img src={img}/>
  </div>
  <div class="product-details">
    <div class="product-title">{data.name}</div>
    <p class="product-description">{data.description}</p>
  </div>
  <div class="product-price">{data.price}</div>
  <div class="product-quantity">
    <input type="number" value={data.Counter} min="1" onChange={changeCounterHandler}/>
  </div>
  <div class="product-removal">
    <button class="remove-product" onClick={removeHandler}> 
      Remove
    </button>
  </div>
  <div class="product-line-price">{data.total}</div>
</div>;
}
 
export default CartProduct;