import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    amount: 0,
    products: [],
    address: "",
    pincode: "",
    name: "",
    user: "",
    phoneNumber: ""
}
export const cartSlice = createSlice({
    name: "cart",
    initialState,
    reducers: {
        setProduct: (state, action) => {
            console.log("slice:", action);
            state.products.push(action.payload);
            let amount = 0;
            state.products.map((ele) => {
                amount = amount + (ele.price) * (ele.Counter);
            })
            state.amount = amount;
        },
        setProductArray: (state, action) => {

            state.products = (action.payload);
            let amount = 0;
            state.products.map((ele) => {
                amount = amount + (ele.price) * (ele.Counter);
            })
            state.amount = amount;
        },
        setCounter: (state, action) => {
            console.log(action.payload)
            let data = action.payload;
            state.products = state.products.map((e) => {
                if (e.productID == data.id) {
                    return {
                        ...e, Counter: data.value , total : data.value*e.price
                    }
                }
                else {
                    return e;
                }
            });
            let amount = 0;
            state.products.map((ele) => {
                amount = amount + (ele.price) * (ele.Counter);
            })
            state.amount = amount;
        },
        setAmount: (state, action) => {
            return state.amount = action.payload;
        },
        setAddress: (state, action) => {
            return state.address = action.payload;
        },
        setUser: (state, action) => {
            return state.user = action.payload;
        },
        setPincode: (state, action) => {
            return state.pincode = action.payload;
        },
        setPhone: (state, action) => {
            return state.phoneNumber = action.payload;
        }
    }
});

export const { setAddress, setAmount, setProduct, setPincode, setPhone, setUser, setProductArray,setCounter } = cartSlice.actions;
export default cartSlice.reducer;