import { useEffect, useState } from "react";
import {useDispatch, useSelector} from 'react-redux';
import { setProduct , setProductArray } from "../Store/CartSlice";
import image from './34778a.webp';
function ProductCart(data){
  
  const cart = useSelector((s)=>s.Cart);
  const [checkCart , setCheckCart] = useState(false);

  console.log(cart);
  const dispatch = useDispatch();

  const ProductDetails = data.data;

  const [Counter , setCounter]  = useState(1);
 

  function handleAddProduct(){
      dispatch(setProduct({...ProductDetails,Counter,total:Counter*ProductDetails.price}));
  }

  useEffect(()=>{
   let flag = false;

   cart.products.map((e)=>{
    if(e.productID == ProductDetails.productID){
        flag = true;
    }
   }) 

   flag ? setCheckCart(true) : setCheckCart(false);

  },[cart]);

  function changeCounter(e){
     setCounter(e.target.value);
  }

  function removeHandler(){
    let itemToLeft = cart.products.filter((e)=>{
        return  e.productID != ProductDetails.productID
    });
    
    console.log(itemToLeft);

    dispatch(setProductArray(itemToLeft));
  }

return <div className='ProductCart'>
<div className="container">
<div className="row">
    <img src={ProductDetails?.img} alt="Product Image" />
</div>
<div className="col-md-6">
    <div className="product-dtl">
        <div className="product-info">
            <div className="product-name">{ProductDetails?.name}</div>
            <div className="reviews-counter">
                <div className="rate">
                    <input type="radio" id="star5" name="rate" defaultValue={5} defaultChecked />
                    <label htmlFor="star5" title="text">5 stars</label>
                    <input type="radio" id="star4" name="rate" defaultValue={4} defaultChecked />
                    <label htmlFor="star4" title="text">4 stars</label>
                    <input type="radio" id="star3" name="rate" defaultValue={3} defaultChecked />
                    <label htmlFor="star3" title="text">3 stars</label>
                    <input type="radio" id="star2" name="rate" defaultValue={2} />
                    <label htmlFor="star2" title="text">2 stars</label>
                    <input type="radio" id="star1" name="rate" defaultValue={1} />
                    <label htmlFor="star1" title="text">1 star</label>
                </div>
                <span>3 Reviews</span>
            </div>
            <div className="product-price-discount"><span>R {ProductDetails?.price}</span><span className="line-through">R {ProductDetails?.beforeSalePrice}</span></div>
        </div>
        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
        {/* <div className="row"> */}
            {/* <div className="col-md-6">
                <label htmlFor="size">Size</label>
                <select id="size" name="size" className="form-control">
                    <option>S</option>
                    <option>M</option>
                    <option>L</option>
                    <option>XL</option>
                </select>
            </div> */}
            {/* <div className="col-md-6">
                <label htmlFor="color">Color</label>
                <select id="color" name="color" className="form-control">
                    <option>Blue</option>
                    <option>Green</option>
                    <option>Red</option>
                </select>
            </div> */}
        {/* </div> */}
        <div className="product-count">
            <label htmlFor="size">Quantity</label>
            <form action="#" className="display-flex">
                <div className="qtyminus" onClick={()=>{setCounter(Counter>1?Counter - 1:1)}} >-</div>
                <input type="text" name="quantity" value={Counter} className="qty" onChange={changeCounter}/>
                <div className="qtyplus" onClick={()=>{setCounter(Counter + 1)}} >+</div>
            </form>
            {
                checkCart?
                <a  className="round-black-btn-remove bg-red-200" onClick={removeHandler}>Remove</a>
                :
                <a  className="round-black-btn " onClick={handleAddProduct}>Add to Cart</a>
            }
        </div>
    </div>
</div>
</div>
</div>;
}
 
export default ProductCart;