import { useState } from "react";
import axios from "axios";
import path from "../../path";
import { useNavigate } from "react-router-dom";
import { useDispatch} from "react-redux";
import { setUser } from "../../Store/userSlice";
import { setModal } from "../../Store/Service";


function OtpLogin(data) {

  const email = data.email;
  const visiblity = data.visiblity;

  const dispatch = useDispatch();
  const navigate = useNavigate();
  


  const [otp, setOtp] = useState("");
  const [verfied, setVerified] = useState(false);

  function otpHandler(e) {
    if (otp.length <= 6) {
      if (e.target.value.length != 7)
        setOtp(e.target.value);
    }
  }

  async function otpSubmit(e) {
    e.preventDefault();
    if (otp.length < 6)
    return
    else {
      try {

        let res = await axios.post(`${path}/forgetlogin`, {
          email:email,
          otp:otp
        }); 

        console.log(res);
        if(res.data.valid){
        if(res.data.user.name)
        {
          setVerified(res.data.user.name);
          setTimeout(()=>{

            visiblity(false);
            navigate("/");

        },5000);

        }
        if(res.data.error == "OTP expired")
        {
          setVerified("User");
        }
        dispatch(setUser(res.data.user));
        }
        else{
          dispatch(setModal(res.data.error));
        } 
      }
      catch (error) {

        alert(error);
        console.log(error);
      }
    }
  }


  return <>
    <div className='Login Otp'>
      <div className="logincard">
        {/* <img src={} alt="Logo-Dairy-Kart" className="Logo" /> */}
        {verfied ? 
         <>
        <h2>Welcome</h2>
        <h3>Password reset successfull</h3>
        <h2 style={{color:"var(--pc)"}}>{verfied}</h2>
        </>
          :
          <>
            <h2>Forget Password</h2>
            <h3>Enter your OTP recieved</h3>
            <form className="loginform">
              <input type="number" placeholder="OTP ( 6 Digits )" value={otp} onChange={otpHandler} />
              <button onClick={otpSubmit}>Validate</button>
              {/* <a style={{ margin: 'auto' }} onClick={()=>{
                resend()
                }}>Resend OTP</a> */}
            </form>
          </>
        }
      </div>
    </div>
  </>;
}

export default OtpLogin;