import { useState } from "react";
import axios from "axios";
import path from "../../path";
import { useNavigate } from "react-router-dom";
import { useDispatch} from "react-redux";
import { setUser } from "../../Store/userSlice";


function Otp(prop) {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const form = prop.formdata;
  const resend = prop.resend;
  const load = prop.load;

  const [otp, setOtp] = useState("");
  const [verfied, setVerified] = useState(false);

  function otpHandler(e) {

    if (otp.length <= 6) {
      if (e.target.value.length != 7)
        setOtp(e.target.value);

    }
  }
  async function otpSubmit(e) {
    e.preventDefault();
    if (otp.length < 6)
      return
    else {
      try {

        load(true);
        let res = await axios.post(`${path}/user`, {
          ...form,
          otp
        });

        
        console.log(res);
        if(res.data.name)
        {
          load(false);
          setVerified(res.data.name);
        }
        if(res.data.error == "OTP expired")
        {
          load(false);
          setVerified("User");
        }


        dispatch(setUser(res.data));
        setTimeout(()=>{
            
            navigate("/");
        },5000);
      }
      catch (error) {
        load(false);
        console.log(error);
      }
    }
  }


  return <>
    <div className='Login Otp'>
      <div className="logincard">
        {/* <img src={} alt="Logo-Dairy-Kart" className="Logo" /> */}
        {verfied ? 
         <>
        <h2>Welcome</h2>
        <h3>Thank you for Registering</h3>
        <h2 style={{color:"var(--pc)"}}>{verfied}</h2>
        </>
          :
          <>
            <h2>Register</h2>
            <h3>Enter your OTP recieved</h3>
            <form className="loginform">
              <input type="number" placeholder="OTP ( 6 Digits )" value={otp} onChange={otpHandler} />
              <button onClick={otpSubmit}>Validate</button>
              <a style={{ margin: 'auto' }} onClick={()=>{
                resend()
                }}>Resend OTP</a>
            </form>
          </>
        }
      </div>
    </div>
  </>;
}

export default Otp;