import './Modal.css';
import { useDispatch,useSelector } from 'react-redux';
import {unSetModal} from '../Store/Service';
function Modal({data}){
const detail = useSelector(state=>state.Modal.content);
const dispatch =  useDispatch();

return <div className='Login Modal'>
<div className="logincard"> 
   <h2>Error</h2>
   <h3 style={{color:"red"}}>{detail}</h3>
   <form className="loginform">
   <button onClick={()=>{dispatch(unSetModal())}}>OK</button>
   {/* <a style={{margin:'auto'}}>Skip now</a> */}
    </form>
</div>

</div>
}


export default Modal;