import './Login.css';
import logo from './../../logo.jpg';
import { useRef, useState } from 'react';
import axios from 'axios';
import path from '../../path';
import { useDispatch } from 'react-redux';
import { setUser } from '../../Store/userSlice';
import { setModal } from '../../Store/Service';
import OtpLogin from './OtpLogin';

function Login({ visiblity }) {
  const [mail,setMail] = useState("");
  const [otpShow , setotpShow] = useState(false);
  const email = useRef();
  const password = useRef();
  const dispatch = useDispatch();


  const [formError, setFormError] = useState({
    email: "",
    password: ""
  });
  const regex = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;

  function handleSubmit(e) {
    e.preventDefault();

    if (!regex.test(email.current.value)) {
      setFormError({ ...formError, email: "Enter valid Email" });
      return;
    }
    setFormError({ email: "", password: "" });
    if (password.current.value.length < 6) {
      setFormError({ ...formError, password: "Password should be more the equals to 6 characters" });
      return;
    }

    setFormError({ email: "", password: "" });

    console.log(email.current.value, password.current);
    handleSendData();
  }

  async function handleSendData() {

    try {
     
      const res = await axios.post(`${path}/login`, {
        email: email.current.value,
        password: password.current.value
      })
      console.log(res);
      if (res.data.valid) {
        dispatch(setUser(res.data.user))
      }
      else {
        dispatch(setModal(res.data.error));
      }
    }
    catch (err) {
      dispatch(setModal("Some Error Occured"));

    }
  }


  async function handleForget(e){
    e.preventDefault();
    if (!regex.test(email.current.value)) {
      setFormError({ ...formError, email: "Enter valid Email to Forget Password" });
      return;
    }
    setFormError({ email: "", password: "" });
    try{

         setMail(email.current.value);
          const res = await axios.post(`${path}/forgetpassword`,{
            email:email.current.value.toLowerCase()
          });
          if(res.data.valid){
             setotpShow(true);
          }
          else{
              dispatch(setModal("Email not Present"))
          }

    }
    catch(error){

    }
  }

  return <>
  <div className='Login'>
    <div className="logincard">
      <img src={logo} alt="Logo-Dairy-Kart" className="Logo" />
      <h2>Login</h2>
      <h3>Enter your credentials</h3>
      <form className="loginform">
        <input type="text" placeholder="Username" required ref={email} />
        <p style={{ color: "red", textAlign: "left" }}>{formError.email}</p>
        <input type="password" placeholder="Password" required ref={password} />
        <p style={{ color: "red", textAlign: "left" }}>{formError.password}</p>
        <a onClick={handleForget}>Forgot your password?</a>
        <button type="submit" onClick={handleSubmit}>LOGIN</button>
        <a style={{ margin: 'auto' , cursor:"pointer"}} onClick={() => { visiblity(false) }}>Skip now</a>
      </form>
    </div>
  </div>
  {
    otpShow ? <OtpLogin email={mail} visiblity={visiblity}/> : ""
  }
  </>;
}

export default Login;