import './Login.css';
import { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { setModal } from '../../Store/Service';
import {Link} from 'react-router-dom';
import Spinner from './Spinner';
import './Signup.css';
import path from '../../path';
import axios from 'axios';
import Otp from './Otp';


function Signup() {
  const initialValues = { username: "", email: "", password: "" , dob:""};
  const [formValues, setFormValues] = useState(initialValues);
  const [formErrors, setFormErrors] = useState({});
  const [isSubmit, setIsSubmit] = useState(false);
  const [showOtp,setShowOtp] = useState(false);
  const [load , setload] = useState(false);
  const dispatch = useDispatch();


  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormValues({ ...formValues, [name]: value });
  }
  const handleSubmit = (e) => {
    e.preventDefault();
    setFormErrors(validate(formValues));
    setIsSubmit(true);
  }

  useEffect(() => {

    if (Object.keys(formErrors).length === 0 && isSubmit) {
      let gender = document.getElementById("male").checked ? "male" : document.getElementById("female") ? "female" : "others" ;
      sendData({...formValues,gender});
    }
  }, [formErrors]);

  async function sendData()
  {
  
   try{
     const emailToSend = formValues.email;
     setload(true);
     let res = await axios.post(`${path}/email`,{email:emailToSend});
     console.log(res);
     setload(false);
     if(res.data.error)
     {
       dispatch(setModal(res.data.error));
     }
     if(res.data.isOtpSend)
     {
      setShowOtp(true);
     }
   }
   catch(error){
      setload(false);
      console.log(error);
      dispatch(setModal("Some Error Occured"))
   }

  }

  const validate = (values) => {
    const errors = {};
    let p = document.getElementById('pass').value
    let c = document.getElementById("confirmpass").value
    const regex = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;

    if (!values.username) {
      errors.username = "Username is required!";
    }
    if (!values.email) {
      errors.email = "Email is required!";
    }
    else if (!regex.test(values.email)) {
      errors.email = "This is not a valid email format!";
    }

    if (!values.password) {
      errors.password = "Password is required!";
    }
    else if (values.password.length < 6) {
      errors.password = "Password must be more than or equals 6 characters";
    }
    else if (values.password.length > 16) {
      errors.password = "Password cannot exceed more than 16 characters";
    }
   
    else if (p !== c)
    {
      errors.confirmPass = "Confirm Password should Match";
      console.log(document.getElementById('pass').value,document.getElementById("confirmpass").value)
      console.log(document.getElementById('pass').value===document.getElementById("confirmpass").value)
    }
    return errors;
  }


 

  return <>
  <div className='Signin'>

    <div className="wrapper login">
      <div className="container">
        <div className="col-left">
          <div className="login-text">
            <h2>Welcome!</h2>
            <h1>Dairy Kart</h1>
            <p>Create your Account<br/>Be a Registered User</p>
          </div>
        </div>
        <div className="col-right">
          <div className="login-form">
            {/* <pre></pre> */}

            <h2>Sign Up</h2>
            {/* <pre>{JSON.stringify(formValues,undefined,2)}</pre> */}
            <form onSubmit={handleSubmit}>

              <p> <label>Username<span>*</span></label> <input type="text" name="username" placeholder="Username " value={formValues.username} onChange={handleChange} /> </p><span className={"error"}>{formErrors.username}</span>
              <p> <label>Email<span>*</span></label> <input type="text" name="email" placeholder="Email Address" value={formValues.email} onChange={handleChange} /> </p>  <span className={"error"}>{formErrors.email}</span>
              <p> <label>Date of Birth<span>*</span></label> <input type="date" placeholder="dd/mm/yyyy" name='dob' required style={{ color: "#666666" }} value={formValues.dob} onChange={handleChange}  min="01-01-1947" max="31-12-2015"/> </p>
              <p className='gender'> <label>Gender<span>*</span></label>
                <label htmlFor="gender" style={{ marginLeft: "10px" }}>Male</label><input type="radio" name='gender' id='male' className='genderradio' />
                <label htmlFor="gender" style={{ marginLeft: "10px" }}>Female</label><input type="radio" name='gender' id='female' className='genderradio' />
                <label htmlFor="gender" style={{ marginLeft: "10px" }}>Others</label><input type="radio" name='gender' id='other' className='genderradio' />
              </p>
              <p> <label>Password<span>*</span></label> <input type="password" name="password" placeholder="Password" value={formValues.password} onChange={handleChange} id="pass"/> </p>  <span className={"error"}>{formErrors.password}</span>
              <p> <label>Confirm Password<span>*</span></label> <input type="password" placeholder="Confirm Password" required id='confirmpass'/> </p> <span className={"error"}>{formErrors.confirmPass}</span>
              <p> <input type="submit" defaultValue="Sign In" />
              </p>
              <p> <Link to={'/'}>Skip for now</Link> </p>

            </form>
          </div>
        </div>
      </div>
    </div>

  </div>;
    {/* OTP Verification*/}
    {
      showOtp?<Otp formdata={formValues} resend={sendData} load={setload}></Otp>:''
    }
    {
      load?<Spinner></Spinner>:""
    }
  </>
}

export default Signup;